.button-text {
  color: #333;
  -webkit-box-shadow: 0 1px 4px rgba(0, 0, 0, 0.4);
          box-shadow: 0 1px 4px rgba(0, 0, 0, 0.4);
  -webkit-animation: loadText 2s ease;
          animation: loadText 2s ease; }

@-webkit-keyframes loadText {
  0% {
    color: #fff;
    -webkit-box-shadow: 0 0px 0px rgba(255, 255, 255, 0.4);
            box-shadow: 0 0px 0px rgba(255, 255, 255, 0.4); }
  100% {
    color: #333;
    -webkit-box-shadow: 0 1px 4px rgba(0, 0, 0, 0.4);
            box-shadow: 0 1px 4px rgba(0, 0, 0, 0.4); } }

@keyframes loadText {
  0% {
    color: #fff;
    -webkit-box-shadow: 0 0px 0px rgba(255, 255, 255, 0.4);
            box-shadow: 0 0px 0px rgba(255, 255, 255, 0.4); }
  100% {
    color: #333;
    -webkit-box-shadow: 0 1px 4px rgba(0, 0, 0, 0.4);
            box-shadow: 0 1px 4px rgba(0, 0, 0, 0.4); } }



