.dropdown-menu a, .dropdown-menu a:hover, .dropdown-menu a:focus {
  text-decoration: none; }

.swipeable-route-wrapper {
  overflow: hidden;
  height: 100%; }

.icon {
  margin: 10;
  font-size: 2rem;
  color: #fff;
  opacity: 0.5; }

.icon:hover {
  opacity: 1;
  -webkit-transition: all 1s ease;
  -o-transition: all 1s ease;
  transition: all 1s ease; }

.swiping {
  margin: 50px;
  position: relative;
  width: 100px; }
  .swiping i {
    position: absolute;
    display: inline-block;
    color: red;
    border-radius: 100%;
    font-family: FontAwesome;
    -webkit-animation: hint-finger ease 2s;
            animation: hint-finger ease 2s;
    -webkit-animation-iteration-count: infinite;
            animation-iteration-count: infinite;
    z-index: 100;
    font-size: 20px; }
  .swiping:before {
    position: absolute;
    content: ' ';
    background: -webkit-gradient(linear, left top, right top, from(#33d9b2), color-stop(#00adb4), color-stop(#347f99), color-stop(#465567), to(#333333));
    background: -webkit-linear-gradient(left, #33d9b2, #00adb4, #347f99, #465567, #333333);
    background: -o-linear-gradient(left, #33d9b2, #00adb4, #347f99, #465567, #333333);
    background: linear-gradient(to right, #33d9b2, #00adb4, #347f99, #465567, #333333);
    right: -34px;
    top: 0;
    width: 0;
    height: 10px;
    -webkit-animation: hint-dot ease 2s;
            animation: hint-dot ease 2s;
    -webkit-animation-iteration-count: infinite;
            animation-iteration-count: infinite; }

@-webkit-keyframes hint-finger {
  0% {
    right: -50px;
    -webkit-transform: scale(1);
            transform: scale(1); }
  20% {
    right: -50px;
    -webkit-transform: scale(0.7);
            transform: scale(0.7); }
  50% {
    right: 50px;
    -webkit-transform: scale(0.7);
            transform: scale(0.7); }
  60% {
    -webkit-transform: scale(1);
            transform: scale(1); }
  70% {
    right: 50px; }
  100% {
    right: -50px;
    -webkit-transform: scale(1);
            transform: scale(1); } }

@keyframes hint-finger {
  0% {
    right: -50px;
    -webkit-transform: scale(1);
            transform: scale(1); }
  20% {
    right: -50px;
    -webkit-transform: scale(0.7);
            transform: scale(0.7); }
  50% {
    right: 50px;
    -webkit-transform: scale(0.7);
            transform: scale(0.7); }
  60% {
    -webkit-transform: scale(1);
            transform: scale(1); }
  70% {
    right: 50px; }
  100% {
    right: -50px;
    -webkit-transform: scale(1);
            transform: scale(1); } }

@-webkit-keyframes hint-dot {
  20% {
    opacity: 0;
    width: 0; }
  50% {
    opacity: .7;
    width: 100px; }
  60% {
    opacity: 0;
    width: 100px; }
  100% {
    opacity: 0;
    width: 0; } }

@keyframes hint-dot {
  20% {
    opacity: 0;
    width: 0; }
  50% {
    opacity: .7;
    width: 100px; }
  60% {
    opacity: 0;
    width: 100px; }
  100% {
    opacity: 0;
    width: 0; } }



